body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.row {
    display: flex;
    box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
    list-style: none;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-flow: row wrap;
    flex-flow: row wrap;
    max-width: 1140px
}

.row .row {
    margin-right: -7.5px;
    margin-left: -7.5px;
    max-width: none
}

@media (max-width:767px) {
    .row .row {
        margin-right: -10px;
        margin-left: -10px
    }
}

.row.reverse {
    flex-direction: row-reverse
}

.row.collapse>.columns {
    padding-right: 0;
    padding-left: 0
}

.row .column,
.row .columns {
    box-sizing: border-box;
    min-width: 0;
    width: 100%;
    flex-grow: 1;
    padding-left: 7.5px;
    padding-right: 7.5px
}

@media (max-width:767px) {

    .row .column,
    .row .columns {
        padding-left: 10px;
        padding-right: 10px
    }
}

.row .column.reverse,
.row .columns.reverse {
    flex-direction: column-reverse
}

