textarea:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px #fff inset !important;
    border: 1px solid #ccc;
    padding-left: 10px;
    color: #000;
    font-family: Roboto, sans-serif !important;
    font-size: .875rem
}

input.inputStyle:focus::-webkit-input-placeholder,
select:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
    color: transparent
}

input.inputStyle:not([type=checkbox]):not([type=radio]):not([type=button]):focus,
select:focus,
textarea:focus {
    outline: none;
    box-shadow: 0 0 5px #c4c5c5;
    transition: box-shadow .5s, border-color .25s ease-in-out
}

textarea {
    resize: none;
    height: 100px;
    box-shadow: none;
    padding: 20px;
    font-size: .8125rem;
    border: 1px solid #b7b8b8;
    width: 100%
}

label {
    color: #000;
    display: inline-block;
    font-size: .75rem;
    margin-bottom: 5px
}

select {
    min-height: 40px;
    color: #c4c5c5;
    font-size: .875rem;
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0
}

legend {
    font-size: .8125rem;
    font-weight: 500;
    margin-bottom: 20px;
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal
}

.select-wrapper:before {
    font-size: 1rem;
    font-family: FontAwesome;
    position: absolute;
    left: 15px;
    top: 11px;
    color: #1c3c49;
    z-index: 10
}

.select-wrapper.location:before {
    content: "\f041"
}

.select-wrapper select {
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding-left: 40px
}

.control {
    display: inline-block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 15px;
    cursor: pointer;
    font-size: .75rem;
    padding-top: 3px;
    width: 20px;
    height: 20px
}

.control input {
    position: absolute;
    z-index: -1;
    left: 0;
    opacity: 0;
    margin-bottom: 0
}

.control input:disabled~.checky:after {
    border-color: #c4c5c5
}

.control.radio .checky {
    border-radius: 50%
}

.control.radio .checky:after {
    left: 5px;
    top: 5px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: #fff
}

.control .checky {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background: #fff;
    border: 1px solid #c4c5c5
}

.control .checky:after {
    content: "";
    position: absolute;
    display: none;
    left: 6px;
    top: 2px;
    width: 5px;
    height: 10px;
    border: solid #fff;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg)
}

.control:hover input~.checky {
    background: rgba(28, 60, 73, .3)
}

.control input:focus~.checky {
    background: #fff
}

.control input:checked~.checky {
    background: #1c3c49;
    border: 1px solid #1c3c49
}

.control input:checked~.checky:after {
    display: block
}

.control:hover input:not([disabled]):checked~.checky {
    background: #0e1e24
}

.control input:checked:focus~.checky {
    background: #1c3c49
}

.control input:disabled~.checky {
    background: #e6e6e6;
    opacity: .6;
    pointer-events: none
}

.checkSwitch {
    -webkit-appearance: none;
    -moz-appearance: none;
    transition: all .4s;
    margin: 0 15px 0 0;
    border-radius: 50px;
    background: #ebebeb;
    width: 35px;
    height: 15px;
    outline: 0;
    cursor: pointer;
    position: relative;
    user-select: none;
    display: inline-block;
    top: 5px
}

.checkSwitchInput {
    position: absolute;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    appearance: none;
    border: none
}

.checkSwitchInput:checked~.checkSwitch {
    background: #1c3c49;
    transition: all .25s
}

.checkSwitchInput:checked~.checkSwitch.dark {
    background: #ece4d5
}

.checkSwitchInput:checked~.checkSwitch:before {
    transform: translate(18px);
    background: #0e1e24
}

.checkSwitchInput:checked~.checkSwitch.dark:before {
    background: #2a5a6e
}

.checkSwitchInput~.checkSwitch:before {
    content: "";
    transition: all .26s;
    position: absolute;
    left: 0;
    top: -3px;
    bottom: 2px;
    right: auto;
    transform: translate(0);
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 40px;
    background: #316980
}

.fileUploadHolder {
    width: auto;
    margin: 20px auto;
    text-align: center
}

.fileUploadHolder .fileInput+label {
    display: inline-block;
    width: auto;
    margin-top: 0;
    vertical-align: top;
    padding: 0;
    background: none;
    font-weight: 600;
    color: #c4c5c5
}

.fileUploadHolder .fileInput+label.hasFile {
    color: #000
}

.fileInput {
    width: .1px !important;
    height: .1px !important;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1
}

.fileInput+label {
    opacity: 1;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    display: inline-block;
    overflow: hidden;
    padding: 10px;
    margin-top: 15px;
    width: auto;
    max-width: 300px;
    text-align: left;
    color: #fff;
    text-transform: none;
    background-color: #1c3c49;
    transition: background .25s ease-in-out
}

.fileInput+label span {
    font-size: .8125rem !important
}

.fileInput+label strong {
    font-style: italic;
    color: #000
}

.fileInput+label+label:hover,
.fileInput+label.has-focus+label,
.fileInput+label:focus+label {
    background-color: #1c3c49
}

.fileInput+label * {
    pointer-events: none
}

@keyframes moveIndeterminate {
    0% {
        background-position: 200% 0
    }

    to {
        background-position: -200% 0
    }
}

progress {
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    border-radius: 3px;
    display: block;
    height: 10px;
    overflow: hidden;
    padding: 0;
    width: 100%;
    background-color: rgba(196, 197, 197, .3)
}

progress::-webkit-progress-bar {
    background-color: rgba(196, 197, 197, .3)
}

progress:indeterminate {
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    animation-name: moveIndeterminate;
    animation-timing-function: linear;
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: 150% 150%
}

progress.primary::-webkit-progress-value {
    background-color: #1c3c49
}

progress.primary::-moz-progress-bar {
    background-color: #1c3c49
}

progress.primary::-ms-fill {
    background-color: #1c3c49;
    border: none
}

progress.primary:indeterminate {
    background-image: linear-gradient(90deg, #1c3c49 30%, rgba(196, 197, 197, .3) 0)
}

progress.secondary::-webkit-progress-value {
    background-color: #000
}

progress.secondary::-moz-progress-bar {
    background-color: #000
}

progress.secondary::-ms-fill {
    background-color: #000;
    border: none
}

progress.secondary:indeterminate {
    background-image: linear-gradient(90deg, #000 30%, rgba(196, 197, 197, .3) 0)
}

progress.tertiary::-webkit-progress-value {
    background-color: #c19a37
}

progress.tertiary::-moz-progress-bar {
    background-color: #c19a37
}

progress.tertiary::-ms-fill {
    background-color: #c19a37;
    border: none
}

progress.tertiary:indeterminate {
    background-image: linear-gradient(90deg, #c19a37 30%, rgba(196, 197, 197, .3) 0)
}

progress.quaternary::-webkit-progress-value {
    background-color: #518793
}

progress.quaternary::-moz-progress-bar {
    background-color: #518793
}

progress.quaternary::-ms-fill {
    background-color: #518793;
    border: none
}

progress.quaternary:indeterminate {
    background-image: linear-gradient(90deg, #518793 30%, rgba(196, 197, 197, .3) 0)
}

.debug * {
    border: 1px solid red
}

.imgFilter {
    display: inline-block
}

.imgFilter img {
    display: block;
    width: 100%;
    height: auto
}

.imgFilter.overlay:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
    width: 100%;
    height: 100%
}

.imgFilter.greyScale {
    filter: grayscale(100%)
}

.imgFilter.blur {
    filter: blur(5px)
}

table {
    border-collapse: collapse;
    border-spacing: 0;
    text-align: left;
    width: 100%
}

table td,
table th,
table tr {
    font-size: .8125rem
}

table th {
    padding: 5px;
    background: rgba(28, 60, 73, .4);
    color: #fff
}

table tbody tr:nth-of-type(odd) {
    background: rgba(196, 197, 197, .2)
}

table tbody td {
    border-bottom: .05rem solid rgba(196, 197, 197, .4);
    padding: 5px
}

section.header .logo {
    width: 100%;
    max-width: 200px;
    margin: 20px 0 5px
}

section.header .logo+p {
    color: #fff;
    margin-top: -10px;
    margin-bottom: 40px
}

nav ul {
    display: flex;
    align-items: stretch;
    justify-content: space-between;
    margin: -20px 0 20px
}

@media (max-width:767px) {
    nav ul {
        display: none
    }
}

nav ul li {
    display: block;
    flex: 0 1 auto;
    list-style-type: none;
    display: inline-flex;
    font-family: Goudy Bookletter\ 1911, serif;
    text-transform: lowercase;
    position: relative
}

nav ul li a:not(.button) {
    font-size: 1rem;
    color: #000;
    position: relative
}

nav ul li a:not(.button):after {
    display: inline-block;
    content: "}";
    font-size: 1.25rem;
    transform: rotate(90deg) translateX(-50%);
    position: absolute;
    left: 50%;
    top: auto;
    bottom: -25px;
    width: 10px;
    opacity: 0;
    height: 100%;
    transition: all .3s ease;
    color: #c19a37
}

nav ul li a:not(.button):active,
nav ul li a:not(.button):focus,
nav ul li a:not(.button):hover {
    color: #000
}

nav ul li a:not(.button):active:after,
nav ul li a:not(.button):focus:after,
nav ul li a:not(.button):hover:after {
    opacity: 1;
    transition: all .3s ease
}

ul li {
    list-style-type: none
}

ul li>strong {
    font-family: Goudy Bookletter\ 1911, serif;
    color: #fff;
    font-size: 1.125rem;
    font-weight: 400
}

ul li>strong span {
    opacity: .2
}

ul li>ul {
    margin-left: 40px
}

@media (max-width:767px) {
    ul li>ul {
        margin-left: 20px
    }
}

ul li>article,
ul li>p {
    margin-left: 40px
}

ul li>article {
    margin-top: 20px;
    margin-bottom: 20px
}


