p.fade {
    cursor: pointer
}

p.fade+span {
    color: #000;
    font-weight: 700;
    font-size: 1rem
}

p.fade.show+span {
    visibility: visible;
    opacity: 1;
    transition: visibility 1s, opacity 1s
}

p.fade.hide+span,
p.fade.show:hover+span {
    visibility: hidden;
    opacity: 0;
    transition: visibility 1s, opacity 1s
}

p.fade.hide:hover+span {
    visibility: visible;
    opacity: 1;
    transition: visibility 1s, opacity 1s
}

